.card {
  background-color: rgb(19, 27, 43);
  max-width: 100%;
  font-family: Inter;
  font-weight: normal;
  font-style: normal;
}

.card-text,
.card-title {
  color: aliceblue;
  max-width: 100%;
}

.card-textiki {
  margin-top: 40px;
  color: aliceblue;
  margin-left: 10px;
  max-width: 100%;
  padding-left: 50px;
  padding-right: 50px;
}
.card-textuc {
  margin-left: 10px;
  text-align: start;
  color: aliceblue;
  max-width: 100%;
  padding-left: 50px;
  padding-right: 55px;
}

.btn.btn-light {
  display: flex;
  border-radius: 1em;
  justify-content: center;
  height: 35px;
  max-width: 100%;
}

.btn.btn-primary {
  display: flex;
  border-radius: 1em;
  justify-content: center;
  height: 35px;
  max-width: 100%;
}

.fontawesome-icon-first {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: transparent;
  cursor: pointer;
  margin: 0.3rem;
  margin-right: 15px;
}

.fontawesome-icon-second {
  display: flex;
  align-items: start;
  justify-content: start;
  background-color: transparent;
  cursor: pointer;
  margin: 0.25rem;
  margin-right: 15px;
}

.fontawesome {
  padding: 1vh 2vh;
  border-radius: 2vh;
  box-sizing: border-box;
  color: #000000;
  background-color: #d4cdcd;
  text-align: center;
  text-decoration: none;
  font-size: 32px;
  cursor: pointer;
  vertical-align: bottom;
  max-width: 100%;
  justify-content: center;
}

.card-footer {
  margin-top: 20px;
  background-color: black;
}
